import React from "react";
import { REMOVE, INCREASE, DECREASE } from "./actions";
import { connect } from "react-redux";

const CartItem = ({
  img,
  title,
  price,
  amount,
  remove,
  increase,
  decrease,
}) => {
  return (
    <div className="cart-item">
      <img src={img} alt={title} />
      <div className="inner-cart-container">
        <h4>{title}</h4>
        <h4 className="item-price">${price}</h4>
        {/* remove button */}
        <button onClick={() => remove()} className="remove-btn">
          remove
        </button>
      </div>
      <div className="inner-cart-container">
        {/* increase amount */}
        <button className="amount-btn" onClick={() => increase()}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
            <path d="M10.707 7.05L10 6.343 4.343 12l1.414 1.414L10 9.172l4.243 4.242L15.657 12z" />
          </svg>
        </button>
        {/* amount */}
        <p className="amount">{amount}</p>
        {/* decrease amount */}
        <button
          className="amount-btn "
          onClick={() => {
            decrease();
          }}
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
            <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
          </svg>
        </button>
      </div>
    </div>
  );
};
const mapDispatchToProps = (dispatch, ownProps) => {
  const { id, amount } = ownProps;
  console.log("props", ownProps);
  // console.log("disp", dispatch);
  return {
    remove: () => dispatch({ type: REMOVE, payload: { id: ownProps.id } }),
    increase: () =>
      dispatch({
        type: INCREASE,
        payload: { id: ownProps.id, amount: amount },
      }),
    decrease: () => dispatch({ type: DECREASE, payload: { id: ownProps.id } }),
  };
};

export default connect(null, mapDispatchToProps)(CartItem);
